import Countdown from 'react-countdown';
import { useEffect, useRef } from 'react';
import { YouboostLogo, NaruzhuLogo, VrubelLogo } from '../../Logo';
import { usePageLoader } from '../hooks/usePageLoader';
import { getRandomTimerNumber } from '../utils/loader';
import { Loader } from './Loader';
import { TelegramBanner } from './TelegramBanner';

const LOGO_MAP: Record<string, JSX.Element> = {
  naruzhu: <NaruzhuLogo className="loader-logo" />,
  youboost: <YouboostLogo className="loader-logo" />,
  'youboost-neo': <YouboostLogo className="loader-logo" />,
  vrubel: <VrubelLogo className="loader-logo" />,
};

const getProjectLogo = (): JSX.Element | null => {
  const extensionCode = process.env.EXTENSION_CODE;

  if (!Object.keys(LOGO_MAP).includes(extensionCode)) {
    return null;
  }
  return LOGO_MAP[extensionCode];
};

export function PageLoader(): JSX.Element | null {
  const timer = useRef(getRandomTimerNumber() * 1000);
  const { isShown, loaderType, handleTimerComplete, isPremiumUser } =
    usePageLoader();

  const ref = useRef<Countdown>(null);

  useEffect(() => {
    function listener(): void {
      if (document.visibilityState === 'visible') {
        ref.current?.start();
      } else {
        ref.current?.pause();
      }
    }
    document.addEventListener('visibilitychange', listener);
    return () => {
      document.removeEventListener('visibilitychange', listener);
    };
  }, []);

  if (!isShown) {
    return null;
  }

  return (
    <div className="page-loader-overlay">
      <div className="loader-content">
        {getProjectLogo()}
        <Loader />
        <p className="loader-text">
          {process.env.EXTENSION_TITLE}{' '}
          {loaderType === 'page'
            ? ' ускоряет YouTube...'
            : ' ускоряет ваше видео'}
        </p>
        {loaderType === 'video' ? (
          <p className="loader-timer">
            Оно откроется через:{' '}
            <Countdown
              ref={ref}
              precision={2}
              date={Date.now() + timer.current}
              renderer={({ seconds }) => <span>{seconds}</span>}
              onComplete={handleTimerComplete}
            />
            секунд
          </p>
        ) : null}
        {!isPremiumUser ? <TelegramBanner /> : null}
      </div>
    </div>
  );
}
